.mainAsset {
  height: 30vh;
  display: flex;
  flex-wrap: wrap;
  background-color: var(--soft-pink);
}

.leftBox {
  width: 50%;
}

.rightBox {
  width: 50%;
}

.rightBox h1 {
  font-family: Lato, sans-serif;
  font-size: 1.2rem;
  font-weight: 700;
  font-stretch: normal;
  font-style: normal;
  line-height: 2.5rem;
  letter-spacing: 1px;
  text-align: left;
  color: var(--white);
  text-transform: uppercase;
}

.marketCap {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.marketCap span {
  margin-left: 10px;
  font-weight: 700;
  font-size: 1.1rem;
  letter-spacing: 1px;
  color: var(--white);
  text-transform: uppercase;
}

.rightBox h2 {
  font-family: Lato, sans-serif;
  font-size: 1rem;
  font-weight: 700;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5rem;
  letter-spacing: 1px;
  text-align: left;
  color: var(--white);
  text-transform: uppercase;
}

.leftBox img {
  width: 120px;
}

.dashGap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 24px;
  background-color: var(--dark-pink);
}

.dashGap p {
  margin-left: 4px;
  font-family: Lato, sans-serif;
  font-size: 0.8rem;
  font-weight: 400;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: -0.48px;
  text-align: left;
  color: var(--white);
  text-transform: uppercase;
}

.goBack {
  position: absolute;
  top: 110%;
  left: 2%;
  width: 25px;
  height: 25px;
  fill: #fff;
}

@media screen and (min-width: 768px) {
  .mainAsset {
    height: 24vh;
  }

  .dashGap {
    height: 2rem;
  }
}
