.mainFilter {
  position: absolute;
  top: 45%;
  left: 50%;
  transform: translateX(-50%);
}

.mainFilter select {
  background: var(--primary-pink);
  border: none;
  width: 105px;
  height: 1rem;
  font-family: Lato, sans-serif;
  font-size: 0.8rem;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: normal;
  text-align-last: center;
  color: var(--white);
}

.mainFilter select:focus {
  outline: none;
}

@media screen and (min-width: 768px) {
  .mainFilter select {
    width: 6rem;
  }
}
